.loader_element {
  position: relative;
  width: 50px;
  height: 50px;
}

.loader_element:before , .loader_element:after{
  content: '';
  border-radius: 50%;
  position: absolute;
  inset: 0;
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.5) inset;
}
.loader_element:after {
  box-shadow: 0 2px 0 #ffd540 inset;
  animation: rotate 2s linear infinite;
}

@keyframes rotate {
  0% {  transform: rotate(0)}
  100% { transform: rotate(360deg)}
}

.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed; 
  z-index: 1; 
  left: 0;
  top: 0;
  width: 100%; 
  height: 100%; 
  overflow: auto; 

  background-color: rgb(15, 15, 17); 
  background-color: rgba(15, 15, 17, 0.4); 

}

