@import "../../../scssStyles/colors";

.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

.content {
  position: relative;
  display: flex;
  justify-content: center;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #fafafa;
  padding: 20px;
  border: 1px solid #888;
  height: 50%;
  width: 50%;
  border-radius: 8px;
  box-shadow: 5px 5px 10px 2px rgba(0, 0, 0, 0.8);
}

.loader {
  transform: translateZ(1px);
  margin-top: 40px;
  margin-bottom: 30px;
}
.loader:after {
  content: "$";
  display: block;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  text-align: center;
  line-height: 40px;
  font-size: 32px;
  font-weight: bold;
  background: #ffd700;
  color: #daa520;
  border: 4px double;
  box-sizing: border-box;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.1);
  animation: coin-flip 4s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
@keyframes coin-flip {
  0%,
  100% {
    animation-timing-function: cubic-bezier(0.5, 0, 1, 0.5);
  }
  0% {
    transform: rotateY(0deg);
  }
  50% {
    transform: rotateY(1800deg);
    animation-timing-function: cubic-bezier(0, 0.5, 0.5, 1);
  }
  100% {
    transform: rotateY(3600deg);
  }
}

.text {
  margin-bottom: 90px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.order {
  margin-top: 10px;
  text-align: center;
  color: #b4bdc3;
}

.home {
  text-align: center;
  margin-top: 20px;
  width: 200px;
  height: 50px;
  text-decoration: none;
  border: 1.5px solid #222;
  border-radius: 50px;
  transition: 500ms;
  box-sizing: border-box;
  cursor: pointer;
}

.link {
  text-decoration: none;
  color: black;
}

.home:hover {
  box-shadow: 0 3px 13px 0 rgba(23, 32, 49, 0.4);
  transition-duration: 0.5s;
  background-color: #222;
  cursor: pointer;
}

.home:hover .link {
  color: white;
}

.close {
  position: absolute;
  top: 20px;
  right: 20px;
  background-color: transparent;
  padding: 0;
  border: none;
}
.closeIcon {
  flex-shrink: 0;
  cursor: pointer;
  path {
    fill: $icons-color;
  }
  &:hover {
    path {
      transition-duration: 0.3s;
      fill: $primary-color;
    }
  }
}
